// 色
$background: #111111; // 背景
$text-color: #ffffff; // テキスト色
$Sub-color: #333333;
$White: #ffffff;
$Black: #000000;
$Yellow: #f9f871;
$Gray: #c4c4c4;

// マウスホバー
$opacity: 0.7; // 透過度

// フォント
$font-family-base: "Noto Sans JP"; // ベースフォント
$font-size-base: 16px; // ベースフォントサイズ
$font-weight-base: 400; // ベースフォントウェイト
$lh: 1.6; // ベース行間

// ---
// レイアウト
// ---
$l-tab: 768px; // タブレットサイズ
$l-inner: 1000px; // インナー幅
$l-pc: 1440px; // パソコン幅

// ブレークポイント（PC基準）
$breakpoints: (
  "sp": "screen and (min-width: 375px)",
  "tab": "screen and (min-width: #{$l-tab})",
  "pc": "screen and (min-width: #{$l-inner})",
  "lg": "screen and (min-width: #{$l-pc})",
);
$padding-pc: 25px; // タブレット時の余白
$padding-sp: 20px; // スマホ時の余白
